body {
  background-color: #F0F2F5;
  margin: 0;
  font-family: 'Arial', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.ocr {
  text-align: center;
  font-size: 4rem;
  font-weight: bold;
  color: #a20101;
  margin-bottom: 1rem;
}

.upload {
  text-align: center;
  margin-bottom: 1rem;
}

.upload input {
  margin-bottom: 1rem;
}
.btn3,
.btn2,
.btn {
  padding: 0.5rem 2.5rem;
  font-size: 1.5rem;
  border-radius: 18px;
  font-weight: bold;
  color: whitesmoke;
  margin-right: 1rem;
  cursor: pointer;
}

.btn2 {
  background-color: #366406;
  border-color: #4CAF50;
}
.btn2:hover {
  transform: scale(1.05);
}
.btn3 {
  background-color: #c21818;
  border-color: #7c3333;
}
.btn3:hover {
  transform: scale(1.05);
}
.btn {
  background-color: #b46e05;
  border-color: #FF9800;
}
.btn:hover {
  transform: scale(1.05);
}
.par {
  display: flex;
  margin: 2rem;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  transition: transform 0.3s ease-in-out;
}

.img {
  flex: 1;
}

.img img {
  width: 100%;
  height: auto;
  max-width: 100%;
  object-fit: cover;
  border-radius: 10px 0 0 10px;
}

.items {
  flex: 1;
  padding: 2rem;
}

.items div {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  padding: 1rem;
}

.items p {
  margin: 0.5rem 0;
}

.items span {
  font-weight: bold;
}

/* Add hover effect for the main container */
.par:hover {
  transform: scale(1.05);
}
